
import { ref, watch } from "vue";
import ApiService from "@/core/services/ApiService";

export default {
  name: "PortSelect",
  props: {
    port_id: {
      type: String,
      default: undefined,
    },
    pol: {
      type: Boolean,
      default: true,
    },
    multiselect: {
      type: Boolean,
      default: false,
    },
  },
  emits: ["update:port_id"],

  setup(props) {
    const element_id = ref(undefined);
    const inputPort = ref({
      loading: false,
      options: [],
      list: [],
    });

    const selectPort = (query) => {
      if (query !== "") {
        setTimeout(() => {
          console.log("query", query);
          inputPort.value.loading = true;
          if (props.pol) {
            ApiService.query("/api/lima/portLoading", {
              params: { rhhafen: query, rheta: "20211013" },
            }).then(({ data }) => {
              inputPort.value.loading = false;
              inputPort.value.list = data;
              inputPort.value.options = data;
            });
          } else {
            ApiService.query("/api/lima/portDischarge", {
              params: { rhhafen: query, rheta: "20211013" },
            }).then(({ data }) => {
              inputPort.value.loading = false;
              inputPort.value.list = data;
              inputPort.value.options = data;
            });
          }
        }, 1000);
      } else {
        inputPort.value.options = [];
      }
    };

    watch(
      () => props.port_id,
      (first) => {
        if (first) {
          element_id.value = first;
        }
      }
    );

    return {
      element_id,
      inputPort,
      selectPort,
    };
  },
};
